<template>

    <v-app-bar color="white" class="mb-5" height="80" >
        
            <router-link to="/"><img :src="theme.organization.logo"  height="80">
            </router-link>            

            <div class="flex-grow-1"></div>


            <!-- <AppAuthMenu class="hidden-tab-and-down"></AppAuthMenu> -->
            <div  class=" "  v-if="isAuthenticated">
                <v-menu
                    v-model="menu"
                    :close-on-content-click="false"
                    :nudge-width="200"
                    offset-x
                    >
                    <template v-slot:activator="{ on }">
                        <v-btn
                        class="notifyBtn"
                        v-on="on"
                        >
                            <v-badge
                            class="notifyBadge"
                        >
                            <i class="fas fa-bell"></i>
                        </v-badge>
                        </v-btn>
                    </template>

                    <v-card>
                        <v-list>
                        <v-list-item>
                            <v-list-item-avatar>
                            <img src="/img/notify.png" alt="notify">
                            </v-list-item-avatar>

                            <v-list-item-content>
                            <v-list-item-title>{{$t('public.notifications')}}</v-list-item-title>
                            <!-- <v-list-item-subtitle>Founder of Vuetify.js</v-list-item-subtitle> -->
                            </v-list-item-content>

                            <v-list-item-action>
                            <v-btn
                                :class="fav ? 'red--text' : ''"
                                icon
                                @click="fav = !fav"
                            >
                                <v-icon>mdi-heart</v-icon>
                            </v-btn>
                            </v-list-item-action>
                        </v-list-item>
                        </v-list>

                        <v-list>
                            <v-list-item class="notificationsList"  v-for="(item, x ) in notificationsData.data" :key="item.id">
                                <v-alert 
                                    dismissible
                                    color="cyan"
                                    border="left"
                                    elevation="2"
                                    colored-border
                                    v-if="item.type=='NewSurvey'"
                                    >
                                    <h4 v-html="item.title"></h4>
                                    {{item.message}}
                                </v-alert>
                                
                                <v-alert
					
                                    dismissible
                                    color="red"
                                    border="left"
                                    elevation="2"
                                    colored-border
                                    v-if="item.type=='CorrectiveAction'"
                                    >
                                     <h4 v-html="item.title"></h4>
                                    {{item.message}}
                                </v-alert>

                                <v-alert
                                
                                    dismissible
                                    color="orange"
                                    border="left"
                                    elevation="2"
                                    colored-border
                                    v-if="item.type=='Closed'"
                                    >
                                        <h4 v-html="item.title"></h4>
                                    {{item.message}}
                                </v-alert> 
                            </v-list-item>
                        </v-list>

                   
                    </v-card>
                    </v-menu>
                


                <!-- <v-avatar>
                    <img
                        src="https://cdn.vuetifyjs.com/images/john.jpg"
                        alt="John"
                    >
                    <h4>hhhhhhhhhh</h4>
                </v-avatar> -->
                <!-- <router-link class="main-nav-link mr-2 ml-2" v-for='item in getAuthNavMenuItems' :key="item.title" :to="item.href" active-class="active" :exact='item.href == "/" ? true : false'>{{item.title}}</router-link>  -->
            </div>

            <!-- <div  class="hidden-tab-and-down mr-4 ml-4"  v-if="!isAuthenticated">
                <router-link class="main-nav-link mr-2 ml-2" v-for='item in getUnAuthNavMenuItems' :key="item.title" :to="item.href" active-class="active" :exact='item.href == "/" ? true : false'>{{item.title}}</router-link> 
            </div> -->


            <AppLogoutModal v-if="isAuthenticated"></AppLogoutModal>

            <div 
                 v-for="entry in languages"
                :key="entry.title"
                @click="changeLocale(entry.language)"
                class="lang-convert mr-4"
            >
                <flag 
                :iso="entry.flag" 
                v-bind:squared=false 
                v-if='entry.language !== locale' 
                />
            </div>

            <!-- <v-btn  icon @click="$emit('open-menu', 'true')"  class=" hidden-tab-and-top mr-2 ml-2" fab>
                <i class="fas fa-bars"></i>
            </v-btn> -->

            

        </v-app-bar>
    
    
</template>

<style lang="scss" scoped>
    header.v-toolbar {
        flex: 0 !important;

        .v-toolbar__content .router-link-active {
            width: 200px;
        }

    }

    .notifyBadge {
        height: 5px;
        width: 5px;
        background: #78c37b;
        border-radius: 100px;

    }

    .notifyBtn {
        background: transparent !important;
        box-shadow: none;
        width: auto;
        min-width: unset !important;
            align-items: baseline;


        &:hover {
            background-color: #fff !important;
        }
    }

    .notificationsList {
        flex-direction: column;
        align-items: baseline;

        div {
            width: 100%;
        }    
    }


    .lang-convert {
        cursor: pointer;
    }

    a.main-nav-link{
        color: var(--brandHTextColor) !important;
        font-size: 1rem;
        padding: 5px 10px;


        &:hover{
            color: var(--brandPrimColor) !important;
        }

        &.active{
            background: var(--brandSecColor) !important;
            border-bottom: 2px solid var(--brandPrimColor) !important;
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
        }
    }

    @media  only screen and (max-width: 993px) {
        .hidden-tab-and-down {
            display: none !important;
        }
    }

    @media  only screen and (min-width: 994px) {
        .hidden-tab-and-top {
            display: none !important;
        }
    }
</style>


<script>

import AppLogoutModal from '@/components/AppLogoutModal.vue';



//VUEX
import { mapGetters } from 'vuex';
import i18n from '@/plugins/i18n';
import Api from '../../service/Api';


import { mdiLogin, mdiLogout  } from '@mdi/js';




export default {
    components: {
        AppLogoutModal,
    }, 
    
    data: () => ({
        languages: [            
            { flag: 'us', language: 'en', title: i18n.messages[i18n.locale].public.en },            
            { flag: 'sa', language: 'ar', title: i18n.messages[i18n.locale].public.ar }       
        ],
        

        svg: {
            login: mdiLogin,
            logout: mdiLogout,
        }, 


        fav: true,
      menu: false,
      message: false,
      hints: true,
       
    }),
    computed: {
        ...mapGetters(['isAuthenticated', 'userNotifications', 'locale', 'theme', "getAuthNavMenuItems", "getUnAuthNavMenuItems", "errors","notificationsData"])
    },
    methods: {
        changeLocale(newLocale) {
            this.$store.commit('SET_LOADING', { name: 'lang', value: true});
            let LocalOrgSlug = process.env.VUE_APP_ORG_SLUG;
            let full = window.location.host
            let parts = full.split('.')
            let subDomain = parts[0]
            let orgSlug = LocalOrgSlug ? LocalOrgSlug : subDomain;
            localStorage.getItem('FBidToken') ? this.$store.dispatch('EDIT_USER_DETAILS', {locale : newLocale}) : false
            this.$store.dispatch('GET_ORG_THEME',{orgSlug: orgSlug, locale: newLocale})
            this.$store.commit('SET_CLEAR_ERROR')
            let x = document.getElementsByClassName('v-messages__message');
            for (let index = 0; index < x.length; index++) {
                const element = x[index];
                element.innerHTML = ""
            }
            setTimeout(() => {
                this.$store.commit('SET_LOADING', { name: 'lang', value: false});
            }, 3000);
           
        },
        
    },
    mounted() {
        this.$store.dispatch('FETCH_NOTIFICATIONS_DATA');
        
    }
}
</script>

